@import '../../index.scss';

.tour{
  background: $mainGrey;
  border-radius: 0.6rem;
  margin: 1rem 0;
  img{
    width: 100%;
    border-top-left-radius: 0.6rem;
    border-top-right-radius: 0.6rem;
    display: block;
  }
}

.image-container{
  position: relative;
}
.close-btn{
  position: absolute;
  display: block;
  top: 5%;
  right : 5%;
  color: $mainGreen;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s linear;
  opacity: 0;
}
.image-container:hover .close-btn{
  opacity: 1;
}
.tour-info{
  padding: 1rem 0.5rem;
  h3{
    text-transform: uppercase;
    color: $mainGreen;
  }
  h4{
    text-transform: uppercase;
    color: $mainBlack;
    margin: 0.2rem 0 0.6rem;
  }
  h5{
    text-transform: capitalize;
    span{
      color: $mainGreen;
      margin: 0.5rem;
      font-size: 1.2rem;
      cursor: pointer;
    }
  }
}